.app {
  position: relative;
  box-sizing: border-box;
}

.app-bg {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: -2;
  background: linear-gradient(#000f, #000e,  #0008),
  url('./bg3.jpg');
  background-position: center;
  background-repeat: no-repeat no-repeat;
  background-size: cover;
}

.app-content {
  z-index: 2;
}