@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Audiowide&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
}

:root {
  background-color: #222;
  font-family: 'Roboto', sans-serif;
  letter-spacing: 0.1rem;
  box-sizing: border-box;

  --input-bg-color: #1814;
  --button-bg-color: #1818;
  --button-disabled-color: #1118;
  --accent-color: #2f2c;
  --dark-color: #181c;
  --text-color: #fffe;
  --text-hint-color: #fff4;
  
  color: var(--text-color);
}

*::-webkit-scrollbar {
  width: 4px;
  height: 4px;
}
 
*::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}
 
*::-webkit-scrollbar-thumb {
  background-color: #2f2c;
  border: 1px solid #181c;
  border-radius: 0.5em;
}

h1, h2, h3 {
  text-transform: uppercase;
  color: var(--accent-color);
  font-family: 'Audiowide', 'Roboto', sans-serif;
}

button, .btn {
  background-color: var(--button-bg-color);
  border: 1px solid var(--accent-color);
  font-family: 'Audiowide', 'Roboto', sans-serif;
  padding: 0.5rem 1rem;
  color: var(--text-color);
  text-transform: uppercase;
  font-weight: bold;
  letter-spacing: 0.1rem;
  cursor: pointer;
  border-radius: 0;
  transition: background-color 400ms ease-in-out;
}

button:disabled, .btn:disabled {
  opacity: 0.4;
  filter: sepia(100%);
}

.btn--secondary {
  font-family: 'Audiowide', 'Roboto', sans-serif;
  background-color: transparent;
  box-shadow: none;
  border: none;
  padding: 0.5rem 1rem;
  color: var(--accent-color);
  text-transform: uppercase;
  font-weight: bold;
  font-family: 'Roboto', sans-serif;
  letter-spacing: 0.1rem;
  cursor: pointer;
}

.btn--tertiary {
  font-family: 'Audiowide', 'Roboto', sans-serif;
  background: linear-gradient( to right,transparent,var(--dark-color),transparent);
  background-color: transparent;
  border-top: 1px solid var(--accent-color);
  border-left: none;
  border-bottom: 1px solid var(--accent-color);
  border-right: none;
  cursor: pointer;
}

button:hover, .btn:hover {
  background-color: var(--dark-color);
}

input, input:focus {
  padding: 0.5rem 1rem;
  border: 1px solid var(--accent-color);
  background-color: var(--input-bg-color);
  color: var(--text-color);
  font-family: 'Roboto', sans-serif;
  letter-spacing: 0.1rem;
}

::placeholder { 
  color: var(--text-color);
  font-family: 'Roboto', sans-serif;
  letter-spacing: 0.1rem;
  opacity: 0.4;
}

.container {
  padding: 2rem;
}

.m0 {
  margin: 0;
}

.mt {
  margin-top: 1rem;
}

.mt-2 {
  margin-top: 1.5rem;
}


.mt-3 {
  margin-top: 2rem;
}

.mb {
  margin-bottom: 1rem;
}

.mb-2 {
  margin-bottom: 1.5rem;
}


.mb-3 {
  margin-bottom: 2rem;
}

.ml {
  margin-left: 1rem;
}

.ml-2 {
  margin-left: 1.5rem;
}


.ml-3 {
  margin-left: 2rem;
}

.mr {
  margin-right: 1rem;
}

.mr-2 {
  margin-right: 1.5rem;
}


.mr-3 {
  margin-right: 2rem;
}

.v-layout {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow: auto;
}

.h-layout {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.flex-grow {
  flex-grow: 1;
}

.justify-left {
  justify-content: flex-start;
}

.justify-stretch {
  justify-content: stretch;
}

.justify-right {
  justify-content: flex-end;
}

.align-left {
  align-items: flex-start;
}

.align-stretch {
  align-items: stretch;
}

.align-right {
  align-items: flex-end;
}

.form-group {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: flex-start;
  margin-bottom: 1rem;
}


.form-group label {
  margin-right: 1rem;
}

.text-xl{
  font-size: 2rem;
}

.text-lg{
  font-size: 1.5rem;
}

.text-sm{
  font-size: 0.75rem;
}

.text-xs{
  font-size: 0.5rem;
}

.text-accent {
  color: var(--accent-color);
}

.text-hint {
  color: var(--text-hint-color);
}

.text-white {
  color: var(--text-color);
}

.text-bold{
  font-weight: bold;
}

table th {
  font-family: 'Audiowide', 'Roboto', sans-serif;
}

.card {
  background-color: #111c;
  border-radius: 0px;
  padding: 1rem;
  border: 1px solid var(--accent-color);
  min-width: 200px;
  box-shadow: inset 0px 0px 40px #0000;
  transition: box-shadow 400ms ease-in-out, transform 400ms ease-in-out;
  animation-name: cardAnim;
  animation-duration: 400ms;
}

@keyframes cardAnim {
  from {transform: scale(0);}
  to {transform: scale(1);}
}

.card:hover {
  box-shadow: inset 0px 0px 40px #2f25;
  transform: scale(1.03);
}

.ReactModal__Overlay {
  opacity: 0;
  transition: opacity 300ms ease-in-out;
}
.ReactModal__Overlay--after-open {
  opacity: 1;
}
.ReactModal__Overlay--before-close {
  opacity: 0;
}

.ReactModal__Content {
  opacity: 0;
  transform: translate(-50%, -100%) scale(0.9);
  transition: all 300ms ease-in-out;
  margin-left: 50%;
  margin-top: 50vh
}

.ReactModal__Content--after-open {
  opacity: 1;
  transform: translate(-50%, -50%) scale(1);
}
.ReactModal__Content--before-close {
  opacity: 0;
  transform: translate(-50%, -100%) scale(0.9);
}

@media screen and (max-width: 426px) {
  .ReactModal__Content {
    opacity: 0;
    transform: translate(0, -100%) scale(0.9);
    transition: all 300ms ease-in-out;
    margin-left: 50%;
    margin-top: 50vh;
    min-width: 95vw;
    max-width: 95vw
  }
  
  .ReactModal__Content--after-open {
    opacity: 1;
    transform: translate(-50%, -50%) scale(1);
  }
  .ReactModal__Content--before-close {
    opacity: 0;
    transform: translate(-50%, -100%) scale(0.9);
  }
}